import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';
import Demo6 from './demo6';
import Demo7 from './demo7';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>DateTime </h1>
                <blockquote className='page-tip'>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo1></Demo1>
                        </Label>
                        <Label className='code-box'>
                            <Demo3></Demo3>
                        </Label>
                        <Label className='code-box'>
                            <Demo5></Demo5>
                        </Label>
                        <Label className='code-box'>
                            <Demo7></Demo7>
                        </Label>
                    </Col>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo2></Demo2>
                        </Label>
                        <Label className='code-box'>
                            <Demo4></Demo4>
                        </Label>
                        <Label className='code-box'>
                            <Demo6></Demo6>
                        </Label>
                    </Col>
                </Row>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>DateTime</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'disabled', desc: '', type: 'boolean', default: 'false'},
                    {param: 'value', desc: '', type: 'string/moment', default: ''},
                    {param: 'format', desc: ' moment', type: 'string', default: 'YYYY-MM-DD HH:mm:ss'},
                    {param: 'startDate', desc: '', type: 'string', default: ''},
                    {param: 'endDate', desc: '', type: 'string', default: ''},
                    {param: 'onSelectDate', desc: '', type: 'function', default: ''},
                    {param: 'onChange', desc: '', type: 'function', default: ''},
                    {param: 'placeholder', desc: 'placeholder', type: 'string', default: ''},
                    {param: 'maxRange', desc: '', type: 'number', default: '0'},
                    {param: 'hourStep', desc: '', type: 'number', default: '1'},
                    {param: 'minute', desc: '', type: 'number', default: '1'},
                    {param: 'secondStep', desc: '', type: 'number', default: '1'},
                    {param: 'theme', desc: ' default dark ', type: 'string', default: 'defalt'}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>getValue()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'String'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setValue(value)</i>
                        <span></span>
                        <ul>
                            <li>
                                value {'String'}  viewformat
                            </li>
                        </ul>
                    </li>

                    <li>
                        <i>hide()</i>
                        <span></span>
                    </li>

                    <li>
                        <i>show()</i>
                        <span></span>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



